@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    margin: 0;
    padding: 0;
}

.app {
    margin: auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    height: 100vh;
}

game-board {
    display: inline-block;
    width: 100%;
}

table {
    border-collapse: collapse;
    margin: auto;
    table-layout: fixed;
}

td {
    width: 50px;
    height: 50px;
    border: 2px solid rgb(206, 206, 206);
    position: relative;

    .dot {
        display: inline-block;
        border-radius: 50%;
        width: 6px;
        height: 6px;
        background-color: black;
        position: absolute;
    }

    .dot-top-left {
        top: -3px;
        left: -3px;
    }

    .dot-top-right {
        top: -3px;
        right: -3px;
    }

    .dot-bottom-left {
        bottom: -3px;
        left: -3px;
    }

    .dot-bottom-right {
        bottom: -3px;
        right: -3px;
    }

    .hitbox {
        display: inline-block;
        position: absolute;
    }

    .hitbox-top {
        width: 90%;
        height: 10%;
        top: -1%;
        right: 50%;
        left: 50%;
        transform: translate(-50%, 0);
    }

    .hitbox-bottom {
        width: 90%;
        height: 10%;
        bottom: -1%;
        right: 50%;
        left: 50%;
        transform: translate(-50%, 0);
    }

    .hitbox-right {
        width: 10%;
        height: 90%;
        right: -1%;
        top: 50%;
        bottom: 50%;
        transform: translate(0, -50%);
    }

    .hitbox-left {
        width: 10%;
        height: 90%;
        left: -1%;
        top: 50%;
        bottom: 50%;
        transform: translate(0, -50%);
    }

}

$blue: blue;
$red: red;
$blueChecked: #9692ff;
$redChecked: #ff9792;

td.clicked-left {
    border-left: 2px solid;
    &.blue-left {
        border-left-color: $blue;
    }
    &.red-left {
        border-left-color: $red;
    }
}

td.clicked-right {
    border-right: 2px solid;
    &.blue-right {
        border-right-color: $blue;
    }
    &.red-right {
        border-right-color: $red;
    }
}

td.clicked-bottom {
    border-bottom: 2px solid;
    &.blue-bottom {
        border-bottom-color: $blue;
    }
    &.red-bottom {
        border-bottom-color: $red;
    }
}

td.clicked-top {
    border-top: 2px solid;
    &.blue-top {
        border-top-color: $blue;
    }
    &.red-top {
        border-top-color: $red;
    }
}

td.selected-left {
    border-left: 2px solid black;
}

td.selected-right {
    border-right: 2px solid black;
}

td.selected-bottom {
    border-bottom: 2px solid black;
}

td.selected-top {
    border-top: 2px solid black;
}

td.background-blue {
    background-color: $blueChecked;
}

td.background-red {
    background-color: $redChecked;
}

